const searcharr = ['白菜','土豆','五花肉','茄子','西红柿'];
export default {
	data() {
		return {

		}
	},
	onLoad(e) {
		this.getData();
	},
	methods: {
		getData(e) {
			var t = this;
			uni.request({
				url: t.$serverUrl + '/search',
				data: { 
					appkey: t.$appkey,
					keyword: searcharr[0],
					num: 20 ,
				},
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log('请求失败', ret)
						return;
					};
					console.log(ret,'数据');
				}
			});
		},
	}
}
